/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

/* Core CSS required for Ionic components to work properly */
@import "@ionic/angular/css/core.css";

/* Basic CSS for apps built with Ionic */
@import "@ionic/angular/css/normalize.css";
@import "@ionic/angular/css/structure.css";
@import "@ionic/angular/css/typography.css";
@import "@ionic/angular/css/display.css";

/* Optional CSS utils that can be commented out */
@import "@ionic/angular/css/padding.css";
@import "@ionic/angular/css/float-elements.css";
@import "@ionic/angular/css/text-alignment.css";
@import "@ionic/angular/css/text-transformation.css";
@import "@ionic/angular/css/flex-utils.css";

/**
 * Ionic Dark Mode
 * -----------------------------------------------------
 * For more info, please see:
 * https://ionicframework.com/docs/theming/dark-mode
 */

/* @import "@ionic/angular/css/palettes/dark.always.css"; */
/* @import "@ionic/angular/css/palettes/dark.class.css"; */
// @import "@ionic/angular/css/palettes/dark.system.css";

@import "assets/icon/manulife-icons.scss";

/* Swiper */
@import "swiper/scss";
@import "swiper/scss/autoplay";
@import "swiper/scss/keyboard";
@import "swiper/scss/pagination";
@import "swiper/scss/scrollbar";
@import "swiper/scss/zoom";

/* font face */
@font-face {
	font-family: "Manulife JH Sans Optimized";
	src: url(assets/font/ManulifeJHSansOptimized_3179380516.ttf) format("truetype");
}

@font-face {
	font-family: "Manulife JH Serif Optimized";
	src: url(assets/font/ManulifeJHSerifOptimized-Italic_818470419.ttf) format("truetype");
}

@font-face {
	font-family: "Noto Sans Khmer Bold";
	src: url(assets/font/NotoSansKhmer-Bold.ttf) format("truetype");
	font-feature-settings: 'liga' 1, 'calt' 1;
}

@font-face {
	font-family: "Noto Sans Khmer ExtraLight";
	src: url(assets/font/NotoSansKhmer-ExtraLight.ttf) format("truetype");
	font-feature-settings: 'liga' 1, 'calt' 1;
}

@font-face {
	font-family: "Noto Sans Khmer Light";
	src: url(assets/font/NotoSansKhmer-Light.ttf) format("truetype");
	font-feature-settings: 'liga' 1, 'calt' 1;
}

@font-face {
	font-family: "Noto Sans Khmer Medium";
	src: url(assets/font/NotoSansKhmer-Medium.ttf) format("truetype");
	font-feature-settings: 'liga' 1, 'calt' 1;
}

@font-face {
	font-family: "manulife-icons";
	src: url(assets/font/manulife-icons.ttf) format("truetype");
}

// max and min width
body {
	min-width: 320px;
}

ion-content {
    --ion-background-color: #f4f4f4;
}

.top-bar-back-icon {
    font-size: 24px;
    color: #282B3E;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 12px;
}

#claim-slider.swiper {
	margin-left: 0px;
	margin-right: 0px;
}

#claim-slider .swiper-slide {
	width: 222px;
	height: 125px;
}

::-webkit-scrollbar {
  display: none;
}

/* For Firefox */
* {
  scrollbar-width: none;
}

ion-modal#uploaddoc-bottompopup-modal {
	--width: 100%;
	--border-radius: 0px;
	--box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
	--ion-backdrop-color: #282b3e;
	--backdrop-opacity: 80%;
}

ion-modal#uploaddoc-bottompopup-modal .ion-page {
	position: relative;
	contain: layout style;
	max-height: 350px;
}
